import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const store = new Vuex.Store({
    state: {
        favorites: null
    },
    getters: {
        favorites: state => state.favorites
    },
    mutations: {
        change(state, favorites) {
            state.favorites = [...state.favorites, favorites]
        }
    },   
})